.button {
  padding: 16px 10px;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  color: @color-grey;
  background-color: inherit;
  border: 2px solid @color-grey;
  text-transform: uppercase;
  cursor: pointer;
  box-sizing: border-box;

  &:hover {
    color: #ffffff;
    background-color: @color-grey;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  &--secondary {
    padding: 18px;
    line-height: 16px;
    color: #ffffff;
    background-color: @color-green;
    border: none;
  }

  &--arrow {
    border: 1px solid #ececec;
  }

  &--arrow:hover {
    background-color: #ffffff;
  }

  &--cart {
    width: 20px;
    height: 20px;
    padding: 0;
    border: none;
  }
}
