.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  background-color: #ffffff;
  box-sizing: border-box;
}

.page__main {
  @media (max-width: 767px) {
    margin-top: 80px;

    &--nojs {
      margin: 0;
    }
  }
}
